@use "../tokens/colors" as colors;
@use "../tokens/components/header" as header;
@use "../tokens/components/parameters-section" as parametersSection;
@use "../tokens/components/main-section" as mainSection;
@use "../tokens/components/graph-container" as graphContainer;
@use "../tokens/components/button" as button;
@use "../tokens/components/text-field" as textField;
@use "../tokens/components/vertice" as vertice;
@use "../tokens/components/menubar" as menubar;
@use "../tokens/components/switch" as switch;
@use "../tokens/components/modal" as modal;
@use "../tokens/components/status-bar" as statusBar;
@use "../tokens/components/document-list" as documentList;
@use "../tokens/components/slider" as slider;
@use "../tokens/components/toast" as toast;
@use "../tokens/size";
@use "../tokens/typography" as font;

:root {
  --body-font-family: #{font.$body-font-family};

  // Button
  --color-background-button-primary-default: #{button.$color-background-button-primary-default-light};
  --color-background-button-primary-hover: #{button.$color-background-button-primary-hover-light};
  --color-background-button-primary-active: #{button.$color-background-button-primary-active-light};
  --color-background-button-primary-disabled: #{button.$color-background-button-primary-disabled-light};
  --color-border-button-primary-default: #{button.$color-border-button-primary-default-light};
  --color-border-button-primary-hover: #{button.$color-border-button-primary-hover-light};
  --color-border-button-primary-active: #{button.$color-border-button-primary-active-light};
  --color-font-button-primary-default: #{button.$color-font-button-primary-default-light};
  --color-font-button-primary-disabled: #{button.$color-font-button-primary-disabled-light};
  --color-font-button-primary-hover: #{button.$color-font-button-primary-hover-light};
  --color-font-button-primary-active: #{button.$color-font-button-primary-active-light};
  --color-background-button-secondary-default: #{button.$color-background-button-secondary-default-light};
  --color-background-button-secondary-hover: #{button.$color-background-button-secondary-hover-light};
  --color-background-button-secondary-active: #{button.$color-background-button-secondary-active-light};
  --color-border-button-secondary-default: #{button.$color-border-button-secondary-default-light};
  --color-border-button-secondary-hover: #{button.$color-border-button-secondary-hover-light};
  --color-border-button-secondary-active: #{button.$color-border-button-secondary-active-light};
  --color-font-button-secondary-default: #{button.$color-font-button-secondary-default-light};
  --color-font-button-secondary-hover: #{button.$color-font-button-secondary-hover-light};
  --color-font-button-secondary-active: #{button.$color-font-button-secondary-active-light};

  --color-background-button-error-default: #{button.$color-background-button-error-default-light};
  --color-background-button-error-hover: #{button.$color-background-button-error-hover-light};
  --color-background-button-error-disabled: #{button.$color-background-button-error-disabled-light};
  --color-border-button-error-default: #{button.$color-border-button-error-default-light};
  --color-border-button-error-hover: #{button.$color-border-button-error-hover-light};
  --color-font-button-error-default: #{button.$color-font-button-error-default-light};
  --color-font-button-error-hover: #{button.$color-font-button-error-hover-light};

  --size-border-button: #{button.$size-border-button};
  --size-border-radius-button: #{button.$size-border-radius-button};
  --size-padding-button: #{button.$size-padding-button};
  --size-padding-button-mobile: #{button.$size-padding-button-mobile};
  --size-font-button: #{button.$size-font-button};
  --size-font-button-mobile: #{button.$size-font-button-mobile};
  --size-width-button: #{button.$size-width-button};
  --size-width-button-mobile: #{button.$size-width-button-mobile};
  --size-max-width-button: #{button.$size-max-width-button};
  --size-max-width-button-mobile: #{button.$size-max-width-button-mobile};
  --font-weight-button: #{button.$font-weight-button};

  --time-transition-button: #{button.$time-transition-button};

  // MenubarTrigger

  --color-background-menutrigger-default: #{menubar.$color-background-menutrigger-default-light};
  --color-background-menutrigger-hover: #{menubar.$color-background-menutrigger-hover-light};
  --color-background-menutrigger-disabled: #{menubar.$color-background-menutrigger-disabled-light};
  --color-background-menutrigger-disabled-hover: #{menubar.$color-background-menutrigger-disabled-hover-light};
  --color-font-menutrigger-default: #{menubar.$color-font-menutrigger-default-light};
  --color-font-menutrigger-hover: #{menubar.$color-font-menutrigger-hover-light};
  --color-font-menutrigger-disabled: #{menubar.$color-font-menutrigger-disabled-light};
  --color-font-menutrigger-disabled-hover: #{menubar.$color-font-menutrigger-disabled-hover-light};

  --color-background-menutrigger-header-default: #{menubar.$color-background-menutrigger-header-default-light};
  --color-background-menutrigger-header-hover: #{menubar.$color-background-menutrigger-header-hover-light};
  --color-background-menutrigger-header-disabled: #{menubar.$color-background-menutrigger-header-disabled-light};
  --color-background-menutrigger-header-disabled-hover: #{menubar.$color-background-menutrigger-header-disabled-hover-light};
  --color-font-menutrigger-header-default: #{menubar.$color-font-menutrigger-header-default-light};
  --color-font-menutrigger-header-hover: #{menubar.$color-font-menutrigger-header-hover-light};
  --color-font-menutrigger-header-disabled: #{menubar.$color-font-menutrigger-header-disabled-light};
  --color-font-menutrigger-header-disabled-hover: #{menubar.$color-font-menutrigger-header-disabled-hover-light};

  --size-padding-menutrigger: #{menubar.$size-padding-menutrigger};
  --size-border-radius-menutrigger: #{menubar.$size-border-radius-menutrigger};
  --size-font-menutrigger: #{menubar.$size-font-menutrigger};
  --size-border-menutrigger-focus: #{menubar.$size-border-menutrigger-focus};

  // Menubar

  --menubar-color: #{colors.$dark};
  --menubar-bg: #{colors.$white};
  --menubar-border-color: #{colors.$white-dark};

  // Menu

  --color-background-menu: #{menubar.$color-background-menu-light};
  --size-padding-menu: #{menubar.$size-padding-menu};
  --size-border-menu: #{menubar.$size-border-menu};
  --size-border-radius-menu: #{menubar.$size-border-radius-menu};
  --box-shadow-menu: #{menubar.$box-shadow-menu-light};
  --time-transition-menu: #{menubar.$time-transition-menu};
  --z-index-menu: #{menubar.$z-index-menu};

  // Switch

  --color-background-switch-default: #{switch.$color-background-switch-default-light};
  --color-background-switch-checked: #{switch.$color-background-switch-checked-light};
  --color-background-switch-thumb-default: #{switch.$color-background-switch-thumb-default-light};
  --color-font-switch: #{switch.$color-font-switch-light};

  // Header

  --color-background-header: #{header.$color-background-header-light};
  --box-shadow-header: #{header.$box-shadow-header-light};

  // Parameters section

  --color-background-parameters-section: #{parametersSection.$color-background-parameters-section-light};
  --color-border-parameters-section: #{parametersSection.$color-border-parameters-section-light};
  --color-border-parameters-section-header: #{parametersSection.$color-border-parameters-section-header-light};
  --color-font-parameter-section-header: #{parametersSection.$color-font-parameter-section-header-light};

  --color-border-bottom-panel-sub-section: #e8e8e9;

  // Main section

  --color-background-main-section: #{mainSection.$color-background-main-section-light};

  // Graph container

  --color-background-graph-container: #{graphContainer.$color-background-graph-container-light};
  --color-border-graph-container: #{graphContainer.$color-border-graph-container-light};
  --box-shadow-graph-container: #{graphContainer.$box-shadow-graph-container-light};

  // TextField

  --color-background-text-field: #{textField.$color-background-text-field-light};
  --color-border-text-field-default: #{textField.$color-border-text-field-default-light};
  --color-border-text-field-focus: #{textField.$color-border-text-field-focus-light};
  --color-font-text-field: #{textField.$color-font-text-field-light};
  --box-shadow-text-field-focus: #{textField.$box-shadow-text-field-focus-light};

  // Label

  --color-font-label: #333333;

  // Vertice

  --color-background-vertice-default: #{vertice.$color-background-vertice-default-light};
  --color-background-vertice-visited: #{vertice.$color-background-vertice-visited-light};
  --color-border-vertice-default: #{vertice.$color-border-vertice-default-light};
  --color-font-vertice: #{vertice.$color-font-vertice-light};
  --box-shadow-vertice-default: #{vertice.$box-shadow-vertice-default-light};
  --box-shadow-vertice-hover: #{vertice.$box-shadow-vertice-hover-light};

  --color-background-vertice-hint-connect-default: #{vertice.$color-background-vertice-hint-connect-default-light};
  --color-background-vertice-hint-connect-hover: #{vertice.$color-background-vertice-hint-connect-hover-light};
  --color-border-vertice-hint-connect-default: #{vertice.$color-border-vertice-hint-connect-default-light};

  // Line (edge)

  --color-stroke-line-edge-default: #4a4a4a;
  --color-stroke-line-edge-traversed: #32cd32;

  // ThemeButton

  --color-background-theme-button: #f8f8f9;
  --color-background-theme-button-hover: #e4e4e5;
  --color-font-theme-button: #616162;
  --color-font-theme-button-hover: #1f1f20;

  // DeleteButton

  --color-background-delete-button: transparent;
  --color-background-delete-button-hover: #b01221;
  --color-font-delete-button: #b01221;
  --color-font-delete-button-hover: #ffffff;

  // Modal

  --color-background-modal: #{modal.$color-background-modal-light};
  --color-font-modal-title: #{modal.$color-font-modal-title-light};
  --color-background-modal-close-hover: #{modal.$color-background-modal-close-hover-light};
  --color-font-modal-close: #{modal.$color-font-modal-close-light};
  --color-font-modal-close-hover: #{modal.$color-font-modal-close-hover-light};

  // Document list

  --color-background-document-list-item-default: #{documentList.$color-background-document-list-item-default-light};
  --color-background-document-list-item-hover: #{documentList.$color-background-document-list-item-hover-light};
  --color-font-document-list-item-default: #{documentList.$color-font-document-list-item-default-light};
  --color-background-document-list-item-selected: #{documentList.$color-background-document-list-item-selected-light};
  --color-font-document-list-item-selected: #{documentList.$color-font-document-list-item-selected-light};

  // Status bar

  --color-background-status-bar: #{statusBar.$color-background-status-bar-light};
  --color-font-status-bar: #{statusBar.$color-font-status-bar-light};
  --color-border-top-status-bar: #{statusBar.$color-border-top-status-bar-light};

  // Slider

  --color-background-slider-default: #{slider.$color-background-slider-default-light};
  --color-background-slider-hover: #{slider.$color-background-slider-hover-light};
  --color-background-slider-thumb-default: #{slider.$color-background-slider-thumb-default-light};
  --color-background-slider-thumb-hover: #{slider.$color-background-slider-thumb-hover-light};
  --box-shadow-slider-thumb-hover: #{slider.$box-shadow-slider-thumb-hover-light};
  --color-font-slider-label-selected: #000000;

  // Toast

  --color-background-toast-success: #{toast.$color-background-toast-success-light};
  --color-background-toast-progress-bar-success: #{toast.$color-background-toast-progress-bar-success-light};
  --color-background-toast-warning: #{toast.$color-background-toast-warning-light};
  --color-background-toast-progress-bar-warning: #{toast.$color-background-toast-progress-bar-warning-light};
  --color-font-toast: #{toast.$color-font-toast-light};
  --toastify-color-success: #4caf51;
  --toastify-color-warning: #{toast.$toastify-color-warning-light};

  --size-height-header: 36px;

  // scrollbar

  ::-webkit-scrollbar-thumb {
    background-color: #e4e4e5;
  }

  ::-webkit-scrollbar-thumb:hover {
    background-color: #616162;
  }
}
